body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search-bar {
    padding: 0.8em .5em;
}

#search-bar {
    width: 100%;
    font-weight: bold;
    border-radius: 55px;
}

.ms-n5 {
    margin-left: -40px;
    border-radius: 55px;
}

.img-fluid {
    height: 150px;
    width: 100%;
    border-radius: 5px;
}

.card {
    border: 0px;
    border-radius: 5px;
}

.img-fluid:hover {
    cursor: pointer;
}

.image {
    padding-left: 0;
}
.sidebar {
    background-color: #5369DB;
    position: fixed;
    width: 400px; /* or 400px */
    height: 100%;
    top: 0;
    left: 0;
    overflow-y: scroll;
    padding: 0 1em;
}

.topButton {
    background-color: white;
    bottom: 40px;
    position: fixed;
    z-index: 2;
    cursor: pointer;
    border-radius: 30px;
    width: 40px;
    height: 40px; 
    transition: opacity 1s ease-in-out;
    box-shadow: 0 9px 25px 0 rgb(132 128 177 / 28%);
    border: none;
    outline: none;
    left: 385px;
}
.canvas {
    height: 800px;
    width: 650px;
    padding: 2em 0;
}
.text-bar {
    top: 0;
    background-color: #fff;
    padding: 1em 0;
}

.Btn {
    margin-right: 1em;
}
